					"content": "<div class=\"ocamldoc\">\n<div class=\"navbar\">&#xA0;<a class=\"up\" href=\"Hashtbl.html\" title=\"Hashtbl\">Up</a>\n&#xA0;<a class=\"post\" href=\"Hashtbl.MakeSeeded.html\" title=\"Hashtbl.MakeSeeded\">Next</a>\n</div>\n<h1>Functor <a href=\"type_Hashtbl.Make.html\">Hashtbl.Make</a></h1>\n\n<pre><span class=\"keyword\">module</span> Make: functor (H : HashedType) -&gt; S  with type key = H.t</pre><div class=\"info module top\">\nFunctor building an implementation of the hashtable structure.\n    The functor <code class=\"code\">Hashtbl.Make</code> returns a structure containing\n    a type <code class=\"code\">key</code> of keys and a type <code class=\"code\">&apos;a t</code> of hash tables\n    associating data of type <code class=\"code\">&apos;a</code> to keys of type <code class=\"code\">key</code>.\n    The operations perform similarly to those of the generic\n    interface, but use the hashing and equality functions\n    specified in the functor argument <code class=\"code\">H</code> instead of generic\n    equality and hashing.  Since the hash function is not seeded,\n    the <code class=\"code\">create</code> operation of the result structure always returns\n    non-randomized hash tables.<br>\n</div>\n<table border=\"0\" cellpadding=\"3\" width=\"100%\">\n<tbody><tr>\n<td align=\"left\" valign=\"top\" width=\"1%%\"><b>Parameters: </b></td>\n<td>\n<table class=\"paramstable\">\n<tbody><tr>\n<td align=\"center\" valign=\"top\" width=\"15%\">\n<code>H</code></td>\n<td align=\"center\" valign=\"top\">:</td>\n<td><code class=\"type\">HashedType</code>\n</td></tr></tbody></table>\n</td>\n</tr>\n</tbody></table>\n<hr width=\"100%\">\n\n<pre id=\"TYPEkey\"><span class=\"keyword\">type</span> key </pre>\n\n\n<pre id=\"TYPEt\"><span class=\"keyword\">type</span> &apos;a t </pre>\n\n\n<pre id=\"VALcreate\">val create : int -&gt; &apos;a t</pre>\n<pre id=\"VALclear\">val clear : &apos;a t -&gt; unit</pre>\n<pre id=\"VALreset\">val reset : &apos;a t -&gt; unit</pre>\n<pre id=\"VALcopy\">val copy : &apos;a t -&gt; &apos;a t</pre>\n<pre id=\"VALadd\">val add : &apos;a t -&gt; key -&gt; &apos;a -&gt; unit</pre>\n<pre id=\"VALremove\">val remove : &apos;a t -&gt; key -&gt; unit</pre>\n<pre id=\"VALfind\">val find : &apos;a t -&gt; key -&gt; &apos;a</pre>\n<pre id=\"VALfind_all\">val find_all : &apos;a t -&gt; key -&gt; &apos;a list</pre>\n<pre id=\"VALreplace\">val replace : &apos;a t -&gt; key -&gt; &apos;a -&gt; unit</pre>\n<pre id=\"VALmem\">val mem : &apos;a t -&gt; key -&gt; bool</pre>\n<pre id=\"VALiter\">val iter : (key -&gt; &apos;a -&gt; unit) -&gt; &apos;a t -&gt; unit</pre>\n<pre id=\"VALfold\">val fold : (key -&gt; &apos;a -&gt; &apos;b -&gt; &apos;b) -&gt; &apos;a t -&gt; &apos;b -&gt; &apos;b</pre>\n<pre id=\"VALlength\">val length : &apos;a t -&gt; int</pre>\n<pre id=\"VALstats\">val stats : &apos;a t -&gt; Hashtbl.statistics</pre></div>"
